@import "vars";
@import "mixins";
@import "fonts";
@import "settings";

// Header start ========================================================================================================================================================

.header {
  padding: 18px 0;
  transition: padding 0.3s ease 0s;
  margin: 0 auto;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 100;
  @media (max-width: 1400px) {
    //padding: 10px 15px;
  }
  &.scroll {
    padding: 10px 0;
    nav.nav {
      > ul {
        > li {
          > a {
            &:after {
              bottom: calc(-100% + 11px);
            }
          }
        }
      }
    }
    .theme {
      width: 35px;
      height: 35px;
    }
  }
  // .header__nav

  &__nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  // .header__logo

  &__logo {
    position: relative;
    z-index: 101;
  }

 // .theme

  .theme {
    border-radius: 50%;
    border: 2px solid rgba(#00B871, 0.7);
    width: 40px;
    height: 40px;
    padding: 5px;
    cursor: pointer;
    position: relative;
    z-index: 101;
    transition: all 0.3s ease 0s;
    svg {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  .nav {
    @media (max-width: 768px){
        position: fixed;
        left: 0;
        top: 0;
        z-index: 100;
        overflow-y: auto;
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        transition: all 0.3s ease 0s;
        transform: scale(0);
        &.menu--active {
          transform: scale(1);
        }
    }
    > ul {
      display: flex;
      @media (max-width: 768px){
          width: calc(100% - 40px);
          height: 60vh;
          margin: 0 auto;
          flex-direction: column;
          align-items: center;
          justify-content: center;
          border-radius: 12px;
          box-shadow: 0px 0px 36px 14px rgba(47, 223, 132, 0.25);
      }
      > li {
        &:not(:first-child) {
          margin-left: 4px;
          @media (max-width: 768px){
              margin-left: 0;
              margin-bottom: 7px;
          }
        }
        @media (max-width: 768px){
            &:first-child {
              margin-bottom: 7px;
            }
        }
        > a {
          display: inline-block;
          padding: 0 13px;
          color: var(--black-color);
          font-size: 16px;
          font-style: normal;
          font-weight: 400;
          line-height: 27px;
          position: relative;
          transition: all 0.3s ease 0s;
          position: relative;
          @media (max-width: 768px){
              font-size: 18px;
          }
          @media (min-width: 769px){
            &:hover {
              color: var(--green-dark);
              &:after {
                width: 100%;
                left: 0;
              }
            }
            &:after {
              content: '';
              position: absolute;
              width: 0%;
              height: 3px;
              background-color: var(--green-dark);
              left: 50%;
              bottom: calc(-100% + 3px);
              transition: all 0.3s ease 0s;
            }
          }
          &.active {
            @media (min-width: 769px){
              color: var(--green-dark);
            }
            &:after {
              content: '';
              width: 100%;
              left: 0%;
            }
          }
        }
      }
    }
    // .nav__list

    &__list {}

    // .nav__item

    &__item {}

    // .nav__link

    &__link {}
  }
  .burger {
    @include burger;

    display: none;
    @media (max-width: 768px){
        display: block;
        z-index: 101;
    }
  }
}

// Header end ========================================================================================================================================================

// Hero section start ========================================================================================================================================================

.hero {
  margin-top: var(--header-height);
  // .hero__wrap

  &__wrap {
    display: flex;
    justify-content: space-between;
    column-gap: 15px;
    padding-top: 100px;
    @media (max-width: 992px){
        flex-direction: column;
        justify-content: flex-start;
        row-gap: 60px;
    }
    @media (max-width: 480px){
        padding-top: 50px;
    }
  }

  // .hero__content

  &__content {
    padding-top: 100px;
    flex-basis: 480px;
    @media (max-width: 992px){
        padding-top: 0;
        flex-basis: 100%;
        text-align: center;
    }
    h1 {
      color: var(--green-dark);
      font-family: 'Gordita';
      font-size: 48px;
      font-style: normal;
      font-weight: 500;
      line-height: normal;
      margin-bottom: 24px;
      span {
        color: var(--grey-color)
      }
      @media (max-width: 400px){
          font-size: 38px;
          text-align: left;
      }
    }
  }

  // .hero__title

  &__title {}

  // .hero__subtitle

  &__subtitle {
    color: var(--grey-color);
    font-family: 'Gordita';
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 28px;
    margin-bottom: 24px;
    max-width: 99%;
    @media (max-width: 400px){
        font-size: 14px;
        max-width: 100%;
        text-align: left;
    }
  }

  // .hero__btn

  &__btn {
    display: inline-block;
    padding: 12px 30px;
    font-size: 16px;
    text-transform: uppercase;
    @media (max-width: 400px){
        padding-left: 0;
        padding-right: 0;
        width: 100%;
        font-size: 14px;
        padding-top: 14px;
        padding-bottom: 14px;
        text-transform: lowercase;
        &::first-letter {
          text-transform: uppercase;
        }
    }
  }

  // .hero__image

  &__image {
    flex-basis: calc(100% - 495px);
    position: relative;
    @media (max-width: 992px){
        flex-basis: 100%;
        align-self: center;
    }
    @media (max-width: 768px){
        max-width: 80%;
        text-align: center;
        img {
          width: 80%;
      }
    }
    @media (max-width: 576px){
        max-width: 100%;
    }
  }

  // .hero__tooltip

  &__tooltip {
    border-radius: 12px;
    background: var(--white-color);
    box-shadow: 0px 9px 20px 0px rgba(0, 0, 0, 0.25);
    display: inline-block;
    padding: 12px 17px 12px 50px;
    position: relative;
    @media (max-width: 480px){
        padding: 8px 10px 8px 35px;
        border-radius: 6px;
    }
    &:before {
      content: '';
      position: absolute;
      left: 12px;
      top: 50%;
      transform: translateY(-50%);
      width: 20px;
      height: 20px;
      border-radius: 50%;
      @media (max-width: 480px){
          width: 15px;
          height: 15px;
      }
    }
    p {
      font-family: 'Inter';
      font-size: 11px;
      font-style: italic;
      font-weight: 400;
      line-height: normal;
      @media (max-width: 480px){
          font-size: 9px;
      }
    }
  }

  // .hero__tooltip-setup

  &__tooltip-setup {
    position: absolute;
    top: 195px;
    left: -50px;
    color: #014451;
    &:before {
      background-color: #16BDCA;
    }
    @media (max-width: 1200px){
        top: auto;
        bottom: 160px;
    }
    @media (max-width: 992px){
        bottom: auto;
        top: 195px;
        left: -50px;
    }
    @media (max-width: 768px){
        left: 0px;
    }
    @media (max-width: 480px){
        top: 100px;
    }
  }

  // .hero__tooltip-delivery

  &__tooltip-delivery {
    position: absolute;
    top: 140px;
    right: 50px;
    color: #771D1D;
    &:before {
      background-color: #FF5A1F;
    }
    @media (max-width: 1200px){
        top: auto;
        bottom: 120px;
        right: 50px;
    }
    @media (max-width: 992px){
        bottom: auto;
        top: 140px;
        right: -20px;
    }
    @media (max-width: 768px){
        right: 0;
    }
  }

  // .hero__tooltip-time

  &__tooltip-time {
    position: absolute;
    top: 230px;
    right: 0;
    color: #014737;
    &:before {
      background-color: #31C48D;
    }
    @media (max-width: 1200px){
        top: auto;
        bottom: 50px;
        right: 100px;
    }
    @media (max-width: 992px){
        bottom: auto;
        top: 230px;
        right: -50px;
    }
    @media (max-width: 768px){
        right: 15px;
    }
    @media (max-width: 480px){
        right: auto;
        left: 40px;
        top: auto;
        bottom: 40px;
    }
  }
}

// Hero section end ========================================================================================================================================================

// Service section start ========================================================================================================================================================

.services {
  background-repeat: no-repeat;
  background-position: 100% 0%;
  @media (max-width: 768px){
    background-size: 100px 70px;
  }
  //padding-top: 128px;

  // .services__wrap

  &__wrap {
    padding-top: 128px;
    padding-bottom: 128px;
    @media (max-width: 992px){
        padding-top: 90px;
        padding-bottom: 90px;
    }
    @media (max-width: 768px){
      padding-top: 50px;
      padding-bottom: 50px;
    }
  }

  // .services__title

  &__title {
    margin-bottom: 12px;
  }

  // .services__subtitle

  &__subtitle {
    color: var(--gray-color);
    text-align: center;
    font-family: 'Gordita';
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 28px;
    max-width: 580px;
    margin: 0 auto 65px auto;
  }

  // .services__items

  &__items {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    row-gap: 50px;
    column-gap: 3%;
    @media (max-width: 768px){
        column-gap: 4%;
    }
  }

  // .services__item

  &__item {
    flex: 0 1 31%;
    text-align: center;
    display: flex;
    flex-direction: column;
    @media (max-width: 768px){
      flex: 0 1 48%;
    }
    @media (max-width: 576px){
        flex: 0 1 100%;
        max-width: 80%;
    }
    @media (max-width: 480px){
        max-width: 100%;
    }
  }

  // .services__img

  &__img {
    margin-bottom: 16px;
    @media (max-width: 480px){
        img {
          width: 100px;
          height: 100px;
        }
    }
  }

  // .services__name

  &__name {
    margin-bottom: 8px;
    color: var(--gray-color);
    font-family: 'Gordita';
    font-size: 24px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    flex-grow: 1;
  }

  // .services__text

  &__text {
    color: var(--gray-color);
    font-family: 'Gordita';
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 27px;
    margin-top: auto;
  }
}

// Service section end ========================================================================================================================================================

// Section how it's work start ========================================================================================================================================================

.how-its-work {
  padding-top: 96px;
  @media (max-width: 992px){
    padding-top: 70px;
  }
  @media (max-width: 768px){
    padding-top: 50px;
  }
  //background-color: #F4FBF7;

  // .how-its-work__wrap

  &__wrap {
    padding-left: calc((100% - var(--content-width)) / 2);
    @media (max-width: 1200px){
        padding-left: 15px;
    }
  }

  // .how-its-work__title

  &__title {
    text-align: center;
    margin-bottom: 12px;
    @media (max-width: 768px){
        padding: 0 15px;
    }
  }

  // .how-its-work__subtitle

  &__subtitle {
    text-align: center;
    font-family: 'Gordita';
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 28px;
    max-width: 560px;
    margin: 0 auto 54px auto;
    @media (max-width: 768px){
      padding: 0 15px;
    }
  }

  // .how-its-work__content

  &__content {
    display: flex;
    column-gap: 64px;
    @media (max-width: 1200px){
        flex-direction: column;
        row-gap: 60px;
    }
    @media (max-width: 768px){
        row-gap: 30px;
    }
  }

  // .how-its-work__list

  &__list {
    flex-basis: 416px;
    @media (max-width: 1200px){
        flex-basis: 100%;
    }
    @media (max-width: 768px){
      padding-right: 15px;
    }
  }

  // .how-its-work__item

  &__item {
    display: flex;
    column-gap: 16px;
    &:not(:last-child) {
      margin-bottom: 32px;
    }
    @media (max-width: 1200px){
        max-width: 80%;
        margin: 0 auto 32px auto;
        &:last-child {
          margin-bottom: 0;
        }
    }
    @media (max-width: 768px){
        max-width: 100%;
    }
  }

  // .how-its-work__icon

  &__icon {
    flex-basis: 64px;
  }

  // .how-its-work__descr

  &__descr {
    flex-basis: calc(100% - 80px);
  }

  // .how-its-work__name

  &__name {
    font-family: 'Gordita';
    font-size: 18px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    margin-bottom: 4px;
  }

  // .how-its-work__text

  &__text {
    font-family: 'Gordita';
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 27px;
    @media (max-width: 480px){
        font-size: 14px;
    }
  }

  // .how-its-work__video

  &__video {
    flex-basis: calc(100% - 480px);
    width: 100%;
    position: relative;
    display: block;
    height: 100% !important;
    border-radius: 8px 0px 0px 0px;
    @media (max-width: 1200px){
        flex-basis: 100%;
    }
    &:after {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      width: 100%;
      height: 100%;
      background-image: linear-gradient(180deg, rgba(47, 223, 132, 0.00) 0%, #CDEFDD 100%);
      z-index: 0;
    }
    img {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      cursor: pointer;
      z-index: 10;
      @media (max-width: 768px){
          width: 50px;
          height: 50px;
      }
      @media (max-width: 400px){
          width: 25px;
          height: 25px;
      }
    }
    a {
      display: block;
      border-radius: 8px 0px 0px 0px;
    }
  }
}

// Section how it's work end ========================================================================================================================================================

// Section price start ========================================================================================================================================================

.price {
  background-repeat: no-repeat;
  background-position: 0% 0%;
  @media (max-width: 992px){
    background-size: 100px 70px;
  }
  //padding-top: 128px;

  // .price__wrap

  &__wrap {
    padding-top: 106px;
    padding-bottom: 64px;
    @media (max-width: 992px){
        padding-top: 80px;
        padding-bottom: 40px;
    }
  }

  // .price__title

  &__title {
    margin-bottom: 12px;
  }

  // .price__subtitle

  &__subtitle {
    color: var(--grey-color);
    text-align: center;
    font-family: 'Gordita';
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 28px;
    max-width: 580px;
    margin: 0 auto 43px auto;
  }

  // .price__btns

  &__btns {
    width: 250px;
    max-width: 250px;
    margin: 0 auto 48px auto;
    display: flex;
    padding: 5px 6px;
    justify-content: center;
    column-gap: 11px;
    border-radius: 14px;
    background-color: var(--white-color);
    border: 1px solid var(--grey-color);
    box-shadow: 1px 1px 2px 0px rgba(73, 63, 63, 0.15);
  }

  // .price__btn

  &__btn {
    flex: 1 1 50%;
    color: var(--grey-color);
    font-family: 'Gordita';
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
  }

  // .price__radio-input

  &__radio-input {
    display: none;
    &:checked + .price__radio-label {
      background-color: var(--green-dark);
      color: var(--white-color);
    }
  }

  // .price__radio-label

  &__radio-label {
    text-align: center;
    display: inline-block;
    width: 100%;
    border-radius: 10px;
    padding: 13px 0;
    cursor: pointer;
    transition: all 0.3s ease 0s;
  }

    // .price__lists

    &__lists {
      overflow: hidden;
    }

    // .price__list

    &__list {
      height: auto;
      transition: all 0.3s ease 0s;
      display: flex;
      justify-content: space-between;
      align-items: flex-end;
      column-gap: 74px;
      padding: 0 20px 40px;
      &.hidden {
        height: 0;
        transform: translateX(-100%) scaleX(0);
        transition: all 0.3s ease 0s;
      }
      @media (max-width: 1200px){
          column-gap: 40px;
      }
      @media (max-width: 992px){
          padding: 0;
          column-gap: 10px;
      }
      @media (max-width: 768px){
          flex-direction: column;
          align-items: center;
          row-gap: 30px;
      }
    }

  // .price__list-month

  &__list-month {}

  // .price__item

  &__item {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    border-radius: 18px;
    border: 2px solid var(--green-light);
    background: var(--white-color);
    padding: 32px;
    transition: all 0.3s ease 0s;
    &:hover {
      box-shadow: 0px 10px 24px 0px rgba(0, 184, 113, 0.50);
      @media (max-width: 992px){
        box-shadow: none;
      }
    }
    @media (max-width: 1200px){
        padding: 32px 20px;
    }
    @media (max-width: 992px){
        padding: 15px;
    }
    @media (max-width: 768px){
        width: 500px;
        max-width: 500px;
        justify-content: center;
        align-items: center;
    }
    @media (max-width: 576px){
        width: 100%;
        max-width: 500px;
    }
  }

  // .price__list-year

  &__list-year {}

  // .price__lists

  &__lists {}

  // .price__list

  &__list {}

  // .price__list-month

  &__list-month {}

  // .price__tariff

  &__tariff {
    color: var(--green-dark);
    font-family: 'Gordita';
    font-size: 32px;
    font-style: normal;
    font-weight: 900;
    line-height: normal;
    text-transform: capitalize;
    margin-bottom: 24px;
    @media (max-width: 992px){
        margin-bottom: 15px;
        font-size: 28px;
    }
    @media (max-width: 768px){
        font-size: 40px;
    }
    @media (max-width: 576px){
        font-size: 32px;
    }
  }

  // .price__price

  &__price {
    color: var(--green-dark);
    font-family: 'Gordita';
    font-size: 48px;
    font-style: normal;
    font-weight: 900;
    line-height: normal;
    text-transform: capitalize;
    margin-bottom: 24px;
    position: relative;
    @media (max-width: 992px){
        font-size: 40px;
        margin-bottom: 15px;
    }
    &:before {
      content: '$';
    }
    span {
      color: var(--grey-color);
      font-family: 'Gordita';
      font-size: 12px;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
    }
  }

  // .price__items

  &__items {
    > li {
      padding-left: 32px;
      background-image: url('../img/check.svg');
      background-repeat: no-repeat;
      background-position: 0 6px;
      background-size: 18px 18px;
    }
    > li:not(:last-child) {
      margin-bottom: 8px;
    }
    margin-bottom: 24px;
  }

  // .price__name

  &__name {
    color: var(--grey-color);
    font-family: 'Gordita';
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: 27px;
    margin-bottom: 5px;
  }

  // .price__text

  &__text {
    color: var(--grey-color);
    font-family: 'Gordita';
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 20px;
  }

  // .price__btn

  &__btn-card {
    padding: 14px 32px;
    &:after {
      left: -100%;
    }
    &:hover:after {
      left: 180%;
    }
    @media (max-width: 992px){
        padding: 10px 20px;
    }
  }

  // .price__item-wrap

  &__item-wrap {
    align-self:stretch;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 24px;
    .price__tariff {
      margin-bottom: 0;
    }
  }

  // .price__popular

  &__popular {
    color: var(--green-dark);
    font-family: 'Inter';
    font-size: 10px;
    font-style: normal;
    font-weight: 900;
    line-height: normal;
    text-transform: capitalize;
    border-radius: 4px;
    background: var(--light-color);
    padding: 4px 10px 4px 26px;
    background-image: url('../img/star.svg');
    background-position: 10px 50%;
    background-repeat: no-repeat;
    background-size: 11px 11px;

  }

  // .price__list-year

  &__list-year {}

}

// Section price end ========================================================================================================================================================

// Section gallery start ========================================================================================================================================================

.gallery {
  padding-top: 115px;
  padding-bottom: 150px;
  background-color: var(--light-color);
  @media (max-width: 1200px){
      padding-top: 90px;
      padding-bottom: 90px;
  }
  @media (max-width: 992px){
      padding-top: 40px;
      padding-bottom: 40px;
  }

  // .gallery__items

  &__items {
    display: flex;
    justify-content: space-between;
    @media (max-width: 768px){
        flex-wrap: wrap;
        justify-content: center;
        row-gap: 20px;
    }
  }

  // .gallery__item

  &__item {
    position: relative;
    overflow: hidden;
    max-width: 379px;
    max-height: 248px;
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
    flex: 0 1 33%;
    @media (max-width: 768px){
        flex-basis: 100%;
    }
  }

  // .gallery__link

  &__link {
    display: block;
    height: 100%;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(0deg, #00B871 0%, rgba(47, 223, 132, 0.00) 100%);
    }
    img {
      display: block;
      height: 100%;
      width: 100%;
      object-fit: cover;
    }
  }

  // .gallery__descr

  &__descr {
    position: absolute;
    left: 24px;
    bottom: 16px;
    @media (max-width: 992px){
        left: 16px;
    }
  }

  // .gallery__head

  &__head {
    color: var(--light-color);
    font-family: 'Gordita';
    font-size: 24px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    @media (max-width: 992px){
        font-size: 20px;
    }
  }

  // .gallery__text

  &__text {
    color: var(--light-color);
    font-family: 'Gordita';
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    @media (max-width: 992px){
        font-size: 12px;
    }
  }
}

// Section gallery end ========================================================================================================================================================

// Section FAQ start ========================================================================================================================================================

.faq {
  background-repeat: no-repeat;
  background-size: 278px 165px;
  background-position: right top;
  padding-top: 78px;
  padding-bottom: 74px;
  @media (max-width: 992px){
      background-size: 120px 70px;
  }

  // .faq__title

  &__title {}

  // .faq__accordion

  &__accordion {
    margin-top: 40px;
  }

  // .faq__subtitle

  &__subtitle {
    margin-top: 45px;
    text-align: center;
    color: var(--gray-color);
    font-family: "Gordita";
    font-size: 24px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
  }

  // .faq__description

  &__description {
    max-width: 560px;
    margin: 12px auto 0 auto;
    color: #374151;
    text-align: center;
    font-family: "Gordita";
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 27px;
    > a {
      color: var(--grey-color);
      white-space: nowrap;
      &:hover {
        text-decoration: underline;
      }
    }
  }
}

.accordion {
  max-width: 980px;
  margin: 40px auto 0 auto;
  width: 100%;
  background: #FFF;

  @media (max-width: 1200px){
      margin-top: 50px;
  }
  // .accordion__content

  &__content {
      margin: 20px 0;
      border: 2px solid #D2D6DC;
      background: var(--white-color);
      overflow: hidden;
      border-radius: 6px;
      transition: all 0.3s ease 0s;
      @media (max-width: 480px){
          margin: 10px 0;
      }
      i {
        transition: all 0.3s ease 0s;
      }
      &:hover {
        border: 2px solid var(--green-dark);
        .accordion__header {
          > i {
            color: var(--green-dark);
        }
      }
      }
      &.open {
          padding-bottom: 32px;
          border: 2px solid var(--green-dark);
          @media (max-width: 768px){
              padding-bottom: 15px;
          }
          .accordion__header {
              min-height: 35px;
              padding-bottom: 15px;
              @media (max-width: 768px){
                  padding-bottom: 10px;
              }
              > i {
                color: var(--green-dark);
            }
          }
      }
  }

  // .accordion__header

  &__header {
      display: flex;
      column-gap: 20px;
      min-height: 50px;
      padding: 32px 24px;
      cursor: pointer;
      align-items: center;
      justify-content: space-between;
      transition: all 0.2s linear;
      @media (max-width: 768px){
          padding: 15px 24px;
      }
      @media (max-width: 480px){
          padding: 15px;
      }
      > i {
          font-size: 15px;
          color: #6B7280;
      }
  }

  // .accordion__title

  &__title {
    color: var(--green-dark);
    font-family: 'Gordita';
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    @media (max-width: 480px){
        font-size: 16px;
    }
  }

  // .accordion__description

  &__description {
    color: var(--gray-color);
    font-family: 'Gordita';
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 27px;
    height: 0;
    padding: 0 10% 0 24px;
    transition: all 0.2s linear;
    @media (max-width: 768px){
        padding-right: 15%;
        font-size: 14px;
        line-height: 22px;
    }
    @media (max-width: 480px){
        padding-right: 20%;
        padding-left: 15px;
    }
  }
}

// Section FAQ end ========================================================================================================================================================

// Section Contact-Us start ========================================================================================================================================================

.contact {
  background-color: var(--light-color);
  background-repeat: no-repeat;
  background-size: 205px 545px;
  background-position: right center;
  padding: 120px 0;
  @media (max-width: 992px){
      background-image: none !important;
  }
  @media (max-width: 768px){
      padding: 70px 0;
  }

  // .contact__wrap

  &__wrap {
    max-width: 910px;
    margin: 0 auto;
    display: flex;
    column-gap: 60px;
    @media (max-width: 992px){
        column-gap: 20px;
    }
    @media (max-width: 768px){
        flex-direction: column;
        row-gap: 40px;
    }
  }

  // .contact__info

  &__info {
    flex: 0 1 50%;
    @media (max-width: 768px){
        flex-basis: 100%;
    }
  }

  // .contact__title

  &__title {
    text-align: left;
    margin-bottom: 9px;
    @media (max-width: 576px){
        text-align: center;
    }
  }

  // .contact__descr

  &__descr {
    color: var(--gray-color);
    font-family: "Gordita";
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 28px;
    max-width: 85%;
    margin-bottom: 40px;
    @media (max-width: 768px){
        max-width: none;
        margin-bottom: 30px;
    }
    @media (max-width: 576px){
        text-align: center;
    }
  }

  // .contact__address

  &__address {
    > ul {
      > li {
        cursor: pointer;
        display: flex;
        align-items: center;
        column-gap: 8px;
        max-width: max-content;
        color: var(--grey-color);
        font-family: "Gordita";
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 16px;
        padding: 8px;
        border-radius: 4px;
        border: 1px solid transparent;
        transition: all 0.3s ease 0s;
        @media (max-width: 576px){
            margin: 0 auto;
            border: 1px solid var(--green-dark);
            padding: 6px 8px;
        }
        svg {
          path {
            transition: all 0.3s ease 0s;
          }
        }
        @media (min-width: 577px){
          &:hover {
            border-radius: 4px;
            border: 1px solid var(--green-dark);
            color: var(--green-dark);
            > a {
              color: var(--green-dark);
            }
            > svg {
              path {
                fill: var(--green-dark);
              }
            }
          }
        }
        > a {
          color: var(--grey-color);
          transition: all 0.3s ease 0s;
        }
        &:not(:last-child) {
          margin-bottom: 23px;
          @media (max-width: 768px){
              margin-bottom: 15px;
          }
        }
        &:last-child {
          margin-bottom: 48px;
          @media (max-width: 768px){
              margin-bottom: 25px;
          }
        }
      }
    }
  }

  // .contact__social

  &__social {
    > ul {
      display: flex;
      column-gap: 10px;
      @media (max-width: 576px){
          justify-content: center;
      }
      > li {
        width: 48px;
        height: 48px;
        border-radius: 50%;
        transition: all 0.3s ease 0s;
        > a {
          display: flex;
          align-items: center;
          justify-content: center;
          width: 100%;
          height: 100%;
          svg {
            path {
              transition: all 0.3s ease 0s;
            }
          }
        }
        &:hover {
          background-color: var(--green-dark);
          > a {
            svg {
              path {
                fill: var(--white-color);
              }
            }
          }
        }
      }
    }
  }

  // .contact__form

  &__form {
    flex: 0 1 50%;
    @media (max-width: 768px){
        flex-basis: 100%;
    }
    form {
      width: 100%;
      border-radius: 16px;
      background: var(--white-color);
      box-shadow: 0px 6px 16px 4px rgba(47, 223, 132, 0.15);
      padding: 32px;
      @media (max-width: 480px){
          padding: 24px 15px;
      }
      label, input, textarea {
        display: block;
      }
      label {
        margin-bottom: 12px;
        color: var(--gray-color);
        font-family: "Gordita";
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 27px;
      }
      input {
        margin-bottom: 24px;
      }
      textarea {
        margin-bottom: 30px;
        height: 180px;
        resize: vertical;
      }
      input, textarea {
        width: 100%;
        padding: 12px 16px;
        border-radius: 11px;
        border: 2px solid #9FA6B2;
        background: var(--white-color);
        box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.15);
        color: var(--gray-color);
        font-family: "Gordita";
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 16px;
        outline: none;
        &:focus {
          border: 2px solid var(--green-dark);
          box-shadow: 0px 0px 0px 4px #2FDF84;
        }
        &::placeholder {
          color: rgba(#161E2E, 0.5);
          font-family: "Gordita";
          font-size: 16px;
          font-style: normal;
          font-weight: 400;
          line-height: 16px;
        }
      }
      button {
        padding: 8px 30px;
        text-transform: uppercase;
        &:after {
          left: -150%;
        }
        &:hover:after {
          left: 200%;
        }
        @media (max-width: 400px){
            width: 100%;
        }
      }
    }
  }
}

// Section Contact-Us end ========================================================================================================================================================

// Footer start ========================================================================================================================================================

.footer {
  background-color: var(--green-dark);

  // .footer__wrap

  &__wrap {
    padding-top: 46px;
    padding-bottom: 64px;
    display: grid;
    grid-template-columns: 2fr repeat(4, 1fr);
    grid-template-rows: auto;
    @media (max-width: 768px){
      grid-template-columns: repeat(4, 1fr);
      column-gap: 25px;
      row-gap: 30px;
      padding: 45px 0;
    }
    @media (max-width: 480px){
      grid-template-columns: repeat(2, 1fr);
    }
    h6 {
      color: var(--white-color);
      font-family: "Gordita";
      font-size: 16px;
      font-style: normal;
      font-weight: 500;
      line-height: normal;
      margin-bottom: 16px;
    }
    ul {
      > li {
        &:not(:last-child) {
          margin-bottom: 12px;
          @media (max-width: 480px){
              margin-bottom: 8px;
          }
        }
        > a {
          color:  rgba(#F4FBF7, 0.9);
          font-family: "Gordita";
          font-size: 12px;
          font-style: normal;
          font-weight: 400;
          line-height: normal;
        }
      }
    }
    a:not(.footer__logo a:first-child) {
      position: relative;
      &:after {
        content: '';
        position: absolute;
        width: 0%;
        height: 1px;
        background-color: rgba(#F4FBF7, 0.9);
        left: 0;
        bottom: -2px;
        transition: all 0.3s ease 0s;
      }
      &:hover:after {
        width: 100%;
      }
    }
  }

  // .footer__logo

  &__logo {
    a {
      color:#FBFDFE;
      font-family: "Gordita";
      font-size: 16px;
      font-style: normal;
      font-weight: 400;
      line-height: normal;
      &:not(:last-child) {
        margin-bottom: 16px;
        display: block;
      }
    }
    @media (max-width: 768px){
      grid-column: 1 / 5;
    }
    @media (max-width: 480px){
      grid-column: 1 / 3;
    }
  }

  // .footer__link

  &__link {

  }

  // .footer__tutoriel

  &__tutoriel {}

  // .footer__help

  &__help {}

  // .footer__company

  &__company {}

  // .footer__copyright

  &__copyright {
    border-top: 1px solid var(--green-light);
  }

  // .footer__copy

  &__copy {
    padding: 30px 0;
    color: #F4FBF7;
    font-family: "Gordita";
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 28px;
    @media (max-width: 768px){
        font-size: 14px;
        text-align: center;
        line-height: 20px;
        padding: 24px 0;
    }
    @media (max-width: 480px){
        max-width: 90%;
        margin: 0 auto;
    }
  }
}


// Footer end ========================================================================================================================================================

.hero__title, .hero__subtitle, .hero__btn, .hero__image > img, .hero__tooltip, .theme {
  opacity: 0;
}

.theme {
  transform: translateY(-200%);
}

.accordion__content {
  transform: translateX(100px);
}
