:root {
  // base
  --font-family: "Gordita", sans-serif;
  --content-width: 1180px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --black-color: #000000;
  --white-color: #ffffff;
  --light-color: #F4FBF7;
  --grey-color: #161E2E;
  --green-dark: #00B871;
  --green-light: #2FDF84;
}
